@font-face {
	font-family: 'WPMU-DEV-Icon';
	src: url('#{$sui-font-path}WPMU-DEV-Icon.eot');
	src: url('#{$sui-font-path}WPMU-DEV-Icon.eot') format('embedded-opentype'),
	url('#{$sui-font-path}WPMU-DEV-Icon.ttf') format('truetype'),
	url('#{$sui-font-path}WPMU-DEV-Icon.woff') format('woff'),
	url('#{$sui-font-path}WPMU-DEV-Icon.woff2') format('woff2'),
	url('#{$sui-font-path}WPMU-DEV-Icon.svg') format('svg');
	font-weight: normal;
	font-style: normal;
}

@include body-class {

	.sui-loading:before {
		animation: spin 1.3s linear infinite;
	}

	// Set the required styles on all icons.
	[class*="sui-icon-"] {
		display: inline-block;
		@include icon(both);
	}

	// Icon sizes.
	[class*="sui-icon-"].sui-xl {
		&:before {
			font-size: 30px;
		}
	}

	[class*="sui-icon-"].sui-lg {
		&:before {
			font-size: 20px;
		}
	}

	[class*="sui-icon-"].sui-md {
		&:before {
			font-size: 16px;
		}
	}

	[class*="sui-icon-"].sui-sm {
		&:before {
			font-size: 12px;
		}
	}

	// Colors.
	[class*="sui-icon-"].sui-success {
		&:before {
			color: inherit;
		}
	}

	[class*="sui-icon-"].sui-error {
		&:before {
			color: inherit;
		}
	}

	[class*="sui-icon-"].sui-warning {
		&:before {
			color: inherit;
		}
	}

	[class*="sui-icon-"].sui-info {
		&:before {
			color: inherit;
		}
	}

	// Fixed width icons.
	[class*="sui-icon-"].sui-fw {
		width: 1.8em;
		text-align: center;
		min-height: 1em;
	}

	// Setup a class name for each icon.
	@each $name, $char in $icons {
		.sui-icon-#{$name}:before {
			content: $char;
		}
	}
}



